@import url(https://fonts.googleapis.com/css?family=Didact+Gothic&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

footer {
    width: 100%;
    height: 50px;
    background: radial-gradient(circle, rgba(0,0,0,0.923406862745098) 0%, rgba(156,156,156,0.9626225490196079) 100%);
    font-family: 'Didact Gothic', sans-serif;
    text-align: center;
    color: white;
}
p{
    padding-top: 10px;
    font-size: 20px
}
.card{
    border-radius: 10px;
    background-color: lightgrey
}

.saved-list{
    border-radius: 15px;
    background-color: lightyellow
}

.deleteBook {
    border-radius: 5px;
    margin-left: 10px
}

.viewBook {
    border-radius: 5px;
    margin-left: 5px
}
.navbar {
    background: radial-gradient(circle, rgba(156,156,156,0.9626225490196079) 0%,rgba(0,0,0,0.923406862745098) 100%);
}

.search:hover {
    background: linear-gradient(273deg, rgba(52,148,230,1) 0%, rgba(236,110,173,1) 100%);
    border: none;
}

.saved:hover {
    background: linear-gradient(94deg, rgba(52,148,230,1) 0%, rgba(236,110,173,1) 100%);
    border: none;
}

.search, .saved {
    border: none;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

form{
    background: lightblue;
    border-radius: 15px;
    margin-top: 3rem;
}
.BookSearchLabel{
    margin-left:20px;
    margin-top: 10px
}
.submitBtn{
    margin-bottom: 10px;
    margin-left: 20px;
}
.form-control{
    margin-left: 20px;
    width: 95%
}
